.navbar_bg {
  background-color: #dddcdc;
  padding: 2rem 0;
  z-index: 99;
  position: relative;
  
}
.navbar_bg1 {
    
    padding: 2rem 0;
    z-index: 99;
    position: relative;
    margin-top: 20px;
    
    
